.conditions-pay__header {
  margin: 0;
  margin-bottom: 15px;
  font-weight: bold;
  font-size: 22px;
  line-height: 32px;
  color: #333333;
}

.conditions-pay__list {
  margin: 0;
  padding: 0;
  list-style: none;
}


.conditions-pay__button {
  width: 100%;
  background-color: transparent;
  border: 1px solid #E0E0E0;
  box-sizing: border-box;
  border-radius: 7px;
  padding: 15px;

  &--first {
    margin-bottom: 12px;
    background-color: #024184;
  }

  &--second {
    padding: 12px;
  }
}

.conditions-pay__button-wrapper {
  width: 154px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;

  &--second {
    width: 170px;
  }
}

.conditions-pay__button-header {
  margin: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #F2F2F2;

  &--second {
    color: #333333;
  }
}

.conditions-pay__button-wrapper--second svg {
  margin-top: -2px;
}

@media (min-width: $tablet-width) {
  .conditions-pay__header {
    margin-bottom: 24px;
    font-size: 26px;
    line-height: 38px;
  }

  .conditions-pay__button {
    width: 180px;
    padding: 14px;

    &--first {
      margin-bottom: 0;
      margin-right: 10px;
      background-color: #024184;
    }

    &--second {
      width: 190px;
    }
  }

  .conditions-pay__button-wrapper--second {
    width: 164px;
  }

  .conditions-pay__list {
    display: flex;
    justify-content: space-between;
    width: 380px;
  }
}

@media (min-width: $desktop-width) {
  .conditions-pay__header {
    margin-bottom: 83px;
    font-size: 34px;
    line-height: 50px;
  }

  .conditions-pay__list {
    flex-direction: column;
    width: 300px;
  }

  .conditions-pay__button {
    width: 255px;
    background-color: transparent;
    border: none;
    border-radius: 0;

    &--first {
      border-bottom: 0.7px solid rgba(79,79,79, 0.15);
      padding-bottom: 25px;
    }

    &--second {
      padding-top: 26px;
    }
  }

  .conditions-pay__button-header {
    color: #01274F;

    &--second {
      color: #333333;
    }
  }

  .conditions-pay__button-wrapper--first svg path {
    stroke: #01274F;
  }

  .conditions-pay__button-wrapper {
    margin-left: 17px;

    &--first {
      margin-left: 18px;
      width: 150px;
    }
  }
}
