.payment {
  background: #FFFFFF;
  border-radius: 7px;
  margin-top: 20px;
}

.payment__description {
  margin: 0;
  font-size: 14px;
  line-height: 22px;
  font-weight: normal;
  color: #333333;

  &--first {
    padding-top: 20px;
  }
}

.payment__wrapper {
  width: 316px;
  margin: 0 auto;
}

.payment__header {
  font-weight: 600;
  font-size: 20px;
  line-height: 29px;
  color: #333333;
  margin: 0;
  margin-top: 15px;
  margin-bottom: 12px;
}

.payment__list,
.pay-system__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.payment__title {
  font-weight: bold;
  font-size: 14px;
  line-height: 22px;
  color: #333333;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 10px;

  &--first {
    margin-top: 10px;
    margin-bottom: 5px;
  }
}

.payment__item {
  padding-bottom: 20px;
  border-bottom: 0.7px solid rgba(79,79,79, 0.15);

  &--bottom {
    border-bottom: none;
  }
}

.pay-system__list {
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  margin-top: 25px;
}

.pay-system__item {
  margin-left: -10px;
}

@media (min-width: $tablet-width) {
  .payment {
    margin-top: 10px;
  }

  .payment__wrapper {
    width: 640px;
  }

  .pay-system__list {
    width: 400px;
  }

  .pay-system__item {
    margin-right: 23.6px;

    &--margin {
      margin-right: 0;
    }
  }
}

@media (min-width: $desktop-width) {
  .payment {
    width: 825px;
    margin-top: 90px;
  }

  .payment__wrapper {
    width: 720px;
    margin-left: 30px;
  }

  .pay-system__list {
    width: 760px;
  }

  .payment__header {
    font-size: 24px;
    line-height: 35px;
    margin: 0;
    margin-top: 23px;
    margin-bottom: 20px;
  }

  .payment__description--first {
    padding-top: 40px;
  }
}
