.page-header {
  position: relative;
  width: 100%;
  min-height: 53px;
  background-color: #01274F;
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  margin-left: 52px;
  margin-right: 22px;
}

.page-header__logo img {
  margin-top: 18px;
  margin-bottom: 17px;
  margin-left: -20px;
  z-index: 1;
}

.page-header__search {
  margin-top: 34px;
  z-index: 1;
}

.page-header__basket {
  margin-top: 33px;
  position: relative;
  z-index: 1;
}

.page-header__number {
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 12px;
  color: white;
  font-size: 10px;
  text-align: center;
  border-radius: 9px;
  height: 17px;
  width: 17px;
  background-color: #FF7900;
}

.page-header__ticker {
  display: none;
}

.page-header__btn-menu {
  padding: 5px;
  position: absolute;
  top: 29px;
  left: 12px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 20px;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.page-header__btn-menu span {
  display: block;
  height: 1.6px;
  background-color: white;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative;
}

.page-header__second {
  padding-bottom: 1px;
}

.page-header__ticker:checked ~ .page-header__btn-menu {
  left: 325px;
  top: 2px;
  width: 16px;
}

.page-header__ticker:checked ~ .main-nav {
  left: 0;
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__first {
  transform: rotate(45deg);
  height: 2px;
  background-color: #4F4F4F;
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__second {
  display: none;
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__third {
  transform: rotate(-45deg);
  top: -7px;
  height: 2px;
  background-color: #4F4F4F;
}

.page-header__favorites,
.page-header__chart,
.page-header__user,
.page-header__list {
  display: none;
}

@media (min-width: $tablet-width) {
  .page-header__favorites,
  .page-header__chart,
  .page-header__user {
    display: block;
  }

  .page-header__basket,
  .page-header__user,
  .page-header__chart,
  .page-header__favorites {
    margin-top: 33px;
    position: relative;
    z-index: 1;
  }

  .page-header__icon {
    display: flex;
    justify-content: space-between;
    margin-right: 43px;
    width: 160px;
  }
}

@media (min-width: $desktop-width) {
  .page-header__btn-menu {
    display: none;
  }

  .page-header__icon {
    margin-right: 0;
  }

  .page-header__wrapper {
    margin-left: 0;
    width: 1085px;
    margin: 0 auto;
    position: relative;
  }

  .page-header__list {
    display: flex;
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0;
    padding-top: 30px;
  }

  .page-header__item a {
    text-decoration: none;
    color: #f2f2f2;
    margin-right: 23px;
    font-weight: normal;
    font-size: 16px;
    line-height: 41px;
    letter-spacing: 0.75px;
    text-transform: uppercase;
  }

  .page-header__search {
    position: absolute;
    right: 180px;
  }

  .page-header__logo {
    margin-left: -105px;
  }
}
