.hidden-menu {
  display: block;
  position: absolute;
  padding-top: 0;
  margin: 0 auto;
  box-sizing: border-box;
  width: 375px;
  background-color: white;
  height: 783px;
  top: 0;
  left: -100%;
  transition: left .2s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.hidden-menu__site-list {
  list-style:none;
  padding: 0;
  margin: 0;
}

.menu__block {
  background-color: #e9e9e9;
  padding-bottom: 35px;
}

.menu-description {
  list-style:none;
  padding: 0;
  margin: 0;
  padding-left: 15px;
  padding-top: 40px;
  padding-bottom: 25px;
  width: 200px;
  text-align: left;
}

.hidden-menu-ticker {
  display: none;
}

.menu-description__item {
  display: flex;
}

.menu-description__item img {
  margin-right: 15px;
}

.menu-description__item p {
  margin: 0;
  font-size: 14px;
  font-weight: lighter;
  margin-bottom: 32px;
}

.menu-description__item--bottom a {
  font-weight: bold;
  margin-bottom: 5px;
  text-decoration: none;
  color: black;
}

.hidden-menu__button {
  font-size: 14px;
  font-weight: lighter;
  padding: 8px 30px 10px 30px;
  border: 2px solid #f83659;
  border-radius: 50px;
  margin-left: 13px;
  background-color: white;
}

.modal-menu {
  display: none;
}


@media (min-width: $tablet-width) {
  .hidden-menu {
    width: 369px;
    height: 1024px;
  }
}
